.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 80px;
  height: 50px;
  background: #2F4F4F; }
  .header .logo {
    height: 30px; }
  .header .navList {
    display: flex;
    justify-content: space-around;
    width: 200px;
    font-size: 14px;
    color: #ccc; }
    .header .navList a:link {
      color: #ccc; }
