.content {
  border-color: #ccc;
  border-radius: 2px;
  background: #fff; }
  .content .nav .navList {
    display: flex;
    padding-left: 10px;
    font-size: 14px;
    background: #eee; }
    .content .nav .navList a {
      padding: 1px 4px;
      border-radius: 4px; }
    .content .nav .navList a:link {
      color: #6495ED; }
    .content .nav .navList a:visited {
      color: #6495ED; }
    .content .nav .navList a:hover {
      color: #008080; }
    .content .nav .navList a:active {
      color: #008080; }
  .content .nav li {
    margin: 14px 12px; }
    .content .nav li .highLight {
      color: #fff !important;
      background: #6495ED; }
  .content .list .listContainer .item {
    position: relative;
    display: flex;
    align-items: center;
    padding: 10px 20px;
    border-bottom: 1px solid #ddd; }
    .content .list .listContainer .item .avatar {
      width: 30px;
      height: 30px; }
      .content .list .listContainer .item .avatar img {
        border-radius: 4px; }
    .content .list .listContainer .item .reading {
      font-size: 14px; }
    .content .list .listContainer .item .title {
      display: flex;
      align-items: center;
      margin-left: 6px;
      font-size: 16px;
      color: #444; }
      .content .list .listContainer .item .title .top, .content .list .listContainer .item .title .share {
        margin-right: 6px;
        padding: 3px 4px;
        font-size: 12px;
        border-radius: 3px; }
      .content .list .listContainer .item .title .top {
        color: #fff;
        background: #6495ED; }
      .content .list .listContainer .item .title .share {
        color: #888;
        background: #eee; }
    .content .list .listContainer .item .time {
      position: absolute;
      right: 10px;
      font-size: 12px;
      color: #888; }
